<template>
<div>
  <div class="uk-article"> 
    <h3 class="uk-article-title">{{ book.title }}</h3> 
    <p class="uk-article-meta">{{ book.house }} - {{book.source}}</p> 
    <br>
  </div>
  <div>
    <form>
      <input type="radio" name="choice" id="paragraph" checked="checked" v-on:mousedown="chooseDisplay('para')">
      <label>In Paragraph</label>
      <input type="radio" name="choice" id="list" v-on:mousedown="chooseDisplay('list')">
      <label>In List (default: 3 items)</label>
    </form>
    <div><span>OR</span></div>
    <label>Word Count (1 to 411)</label>
    <input type="text" id="word" v-model="countVal">
    <input class="uk-button uk-button-default uk-button-small" type="button" name="choiceCount" value="y"id="count" v-on:mousedown="chooseDisplay('count')">
  </div>
  <div>
    <textarea rows="10" cols="80" id="content" >{{ displaying }}</textarea>
    <br>
    <button class="uk-button uk-button-primary" v-on:mousedown="copyContent()">Copy</button>
  </div>
</div>
</template>

<script>
export default {
  name: 'Book',
  data () {
    return {
      book: {
        title: 'ä¹é˜´çœŸç»',
        house: 'unknown',
        source: 'å°„é›•è‹±é›„ä¼ ',
        content: 'å¤©ä¹‹é“ï¼ŒæŸæœ‰ä½™è€Œè¡¥ä¸è¶³ï¼Œæ˜¯æ•…è™šèƒœå®ï¼Œä¸è¶³èƒœæœ‰ä½™ã€‚å…¶æ„åšï¼Œå…¶ç†å¥¥ï¼Œå…¶è¶£æ·±ï¼Œå¤©åœ°ä¹‹è±¡åˆ†ï¼Œé˜´é˜³ä¹‹å€™åˆ—ï¼Œå˜åŒ–ä¹‹ç”±è¡¨ï¼Œæ­»ç”Ÿä¹‹å…†å½°ï¼Œä¸è°‹è€Œé—è¿¹è‡ªåŒï¼Œå‹¿çº¦è€Œå¹½æ˜æ–¯å¥‘ï¼Œç¨½å…¶è¨€æœ‰å¾®ï¼ŒéªŒä¹‹äº‹ä¸å¿’ï¼Œè¯šå¯è°“è‡³é“ä¹‹å®—ï¼Œå¥‰ç”Ÿä¹‹å§‹çŸ£ã€‚å‡è‹¥å¤©æœºè¿…å‘ï¼Œå¦™è¯†ç„é€šï¼Œæˆè°‹è™½å±ä¹ç”ŸçŸ¥ï¼Œæ ‡æ ¼äº¦èµ„äºæ²»è®­ï¼Œæœªå°æœ‰è¡Œä¸ç”±é€ï¼Œå‡ºä¸ç”±äº§è€…äº¦ã€‚ç„¶åˆ»æ„ç ”ç²¾ï¼Œæ¢å¾®ç´¢éšï¼Œæˆ–è¯†å¥‘çœŸè¦ï¼Œåˆ™ç›®ç‰›æ— å…¨ï¼Œæ•…åŠ¨åˆ™æœ‰æˆï¼ŒçŠ¹é¬¼ç¥å¹½èµï¼Œè€Œå‘½ä¸–å¥‡æ°ï¼Œæ—¶æ—¶é—´å‡ºç„‰ã€‚ äº”è—å…­åºœä¹‹ç²¾æ°”ï¼Œçš†ä¸Šæ³¨äºç›®è€Œä¸ºä¹‹ç²¾ã€‚ç²¾ä¹‹æ¡ˆä¸ºçœ¼ï¼Œéª¨ä¹‹ç²¾ä¸ºç³å­ï¼Œç­‹ä¹‹ç²¾ä¸ºé»‘çœ¼ï¼Œè¡€ä¹‹ç²¾åŠ›ç»œï¼Œå…¶æ¡ˆæ°”ä¹‹ç²¾ä¸ºç™½çœ¼ï¼Œè‚Œè‚‰ä¹‹ç²¾ä¸ºçº¦æŸï¼Œè£¹æ’·ç­‹éª¨è¡€æ°”ä¹‹ç²¾è€Œä¸è„‰å¹¶ä¸ºç³»ï¼Œä¸Šå±äºè„‘ï¼Œåå‡ºäºé¡¹ä¸­ã€‚æ•…é‚ªä¸­äºé¡¹ï¼Œå› é€¢å…¶èº«ä¹‹è™šï¼Œå…¶äººæ·±ï¼Œåˆ™éšçœ¼ç³»ä»¥å…¥äºè„‘ï¼Œå…¥æ‰‹è…¼åˆ™è„‘è½¬ï¼Œè„‘è½¬åˆ™å¼•ç›®ç³»æ€¥ï¼Œç›®ç³»æ€¥åˆ™ç›®çœ©ä»¥è½¬çŸ£ã€‚é‚ªå…¶ç²¾ï¼Œå…¶ç²¾æ‰€ä¸­ä¸ç›¸æ¯”äº¦åˆ™ç²¾æ•£ï¼Œç²¾æ•£åˆ™è§†å²ï¼Œè§†å²è§ä¸¤ç‰©ã€‚ é˜´æåœ¨å…­ï¼Œä½•ä»¥è¨€ä¹ã€‚å¤ªæç”Ÿä¸¤ä»ªï¼Œå¤©åœ°åˆåˆ¨åˆ¤ã€‚å…­é˜´å·²æï¼Œé€¢ä¸ƒå½’å…ƒå¤ªç´ ï¼Œå¤ªç´ è¥¿æ–¹é‡‘å¾·ï¼Œé˜´ä¹‹æ¸…çº¯ï¼Œå¯’ä¹‹æ¸Šæºã€‚'
      },
      countVal: 0,
      listCount: 3,
      displaying: ''
    }
  },
  created: function () {
    this.displaying = this.book.content
  },
  methods: {
    copyContent: function () {
      var content = document.querySelector('#content')
      content.select()
      var successful = document.execCommand('copy')
      var message = successful ? 'success' : 'failed'
      console.log('copy text is ' + message)
      console.log(successful)
    },
    chooseDisplay: function (displayStyle) {
      if (displayStyle === 'list') {
        var tmp = ''
        for (var i = 0; i < this.listCount - 1; i++) {
          tmp = tmp.concat(tmp, '<li>' + this.book.content.split('ã€‚')[i] + '</li>')
        }
        this.displaying = tmp
      } else if (displayStyle === 'count') {
        console.log(this.countVal)
        if (this.countVal < 0 || this.countVal > 411) {
          this.displaying = this.book.content
        } else {
          this.displaying = this.book.content.substring(0, this.countVal)
        }
      } else {
        this.displaying = this.book.content
        console.log(this.displaying.length)
      }
    }
  }
}
</script>

